import React from "react";
import LogoutButton from "../components/LogoutButton";
import { useAuth0 } from "@auth0/auth0-react";
import { useEffect, useState } from "react";
import background from "../images/contentBg.jpg";
import {
  Container,
  H1,
  H2,
  Buttons,
  Button,
  Background,
} from "../components/ContentElements";

function Content() {
  const { isAuthenticated } = useAuth0();
  const [joke, setJoke] = useState("");
  const [punchline, setPunchLine] = useState("");
  const [displayAnswer, setDisplayAnswer] = useState({
    answer: false,
    opacity: 0,
  });
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    fetch("https://official-joke-api.appspot.com/jokes/programming/random")
      .then((res) => res.json())
      .then((res) => {
        setJoke(res[0].setup);
        setPunchLine(res[0].punchline);
        setLoaded(true);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);

  return isAuthenticated && loaded ? (
    <>
      <Background
        style={{
          backgroundImage: `url(${background}`,
        }}
      ></Background>
      <Container>
        <H1>{joke}</H1>
        <H2 style={{ opacity: displayAnswer["opacity"], transition: "2s" }}>
          {displayAnswer.answer ? punchline : ""}
        </H2>
        <Buttons>
          <Button
            onClick={() =>
              setDisplayAnswer({
                answer: true,
                opacity: 1,
              })
            }
          >
            Answer
          </Button>
          <LogoutButton />
        </Buttons>
      </Container>
    </>
  ) : (
    ""
  );
}

export default Content;
