import "./App.css";
import { useAuth0 } from "@auth0/auth0-react";
import Home from "./pages/Home";
import Content from "./pages/Content";
import Loading from "./components/Loading";

function App() {
  const { isLoading } = useAuth0();

  if (isLoading) return <Loading />;

  return (
    <>
      <Home />
      <Content />
    </>
  );
}

export default App;
