import React from "react";
import LoginButton from "../components/LoginButton";
import {
  Background,
  Container,
  Heading,
  SubHeading,
} from "../components/HomeElements";
import { useAuth0 } from "@auth0/auth0-react";
import background from "../images/homeBg.jpg";

function Home() {
  const { isAuthenticated } = useAuth0();
  return (
    !isAuthenticated && (
      <>
        <Background
          style={{
            backgroundImage: `url(${background}`,
          }}
        ></Background>
        <Container>
          <Heading>Random Joke Generator</Heading>
          <SubHeading>Click 'Login' to get yours </SubHeading>
          <LoginButton />
        </Container>
      </>
    )
  );
}

export default Home;
